.FP ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  margin: 0.4em 0;
  padding: 0;

  list-style-type: none;

  font-size: 1.5em;
}

.FP span {
  margin-right: 0.2em;

  text-shadow: 5px 5px 10px #00000080;
}

.list__container {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-around;
}

.list__item {
  display: flex;
  gap: 0.5em;
  align-items: center;
}

.list__label {
  font-size: 0.7em;
  color: #f6f6f6;

  border-bottom: 2px solid transparent;
  transition: border-bottom 0.5s ease;
}

.list__label:hover {
  border-bottom: 2px solid #707070;
}

.inline {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.inline p {
  margin-bottom: 0;

  color: #eac56b;
  font-size: 1em;
  font-weight: 600;
}

.copy__button {
  padding: 0.2em;

  border-radius: 0.6em;
  background: #474747;
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.5);

  display: flex;
  align-items: center;

  transition: linear 0.5s;
}

.copy__button:hover {
  text-decoration: none;
  background-color: #2f2f2f;

  cursor: pointer;
}

.copy__button:active {
  transform: scale(0.8);
}

.copy__button svg {
  height: 20px;
  width: 20px;
}

.dropdown__content {
  display: flex;
  flex-wrap: wrap;
}

.dropdown__content .custom-checkbox__container {
  margin-right: 0.3em;
}

.dropdown__content .checkbox-label {
  flex-direction: row-reverse;

  font-size: 0.8em;
}

.dropdown__content ul {
  font-size: 1.3em;

  margin: 0.2em;
}

.FP .dropdown__content li {
  margin: 0.3em 0;
}
