.QR,
form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.3em;
}

form .report__input {
  color: #f6f6f6;
}

.QR__select {
  color: #f6f6f6;
  font-size: 0.7em;

  background: none;
  border: none;
  border-bottom: 2px solid #474747;

  transition: border-bottom-color 0.3s;
}

.QR__select:hover,
:focus {
  border-bottom-color: #eac56b;

  cursor: pointer;
}

.QR__option {
  background: #2f2f2f;
  border: none;
}

form .report__button {
  font-size: 0.55em;
}
